@keyframes bg-animation {
  from {
    background-color: #ee787f;
  }
  to {
    background-color: #ffffff;
  }
}

@keyframes slide-up {
  from {
    transform: translate3d(0, 40px, 0);
    color: transparent;
    opacity: 0;
  }
  to {
    transform: translate3d(0, 0, 0);
    color: #ee787f;
    opacity: 1;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-in-out {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

html,
body {
  font-size: 16px;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: #ee787f;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; */
  font-family: "Baloo Tammudu 2", cursive;

  animation: bg-animation 3s ease 1s forwards;
}

.center-container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  animation: fade-in 1s ease forwards, slide-up 1s ease forwards;
}

.logo {
  width: 800px;
  max-width: 80%;
}

.info-container {
  bottom: 0;
  left: 0;
}

.info {
  transform: translate3d(0, 40%, 0);
  color: transparent;
  opacity: 0;

  animation: slide-up 2s ease 3s forwards;

  h1 {
    padding: 0;
    margin: 0;
    font-size: 1.2rem;
    
    // Medium devices (tablets, 768px and up)
    @media (min-width: 768px) {
        font-size: 2.5rem;
        line-height: 4rem;
    }
  }

  .charging {
    opacity: 0;


    animation: fade-in 2s ease-in-out 5s forwards;
  }
}
